import React from 'react';

import { Input, Select, DatePicker } from 'components';

import styles from './index.less';

const { Textarea } = Input;
const Option = Select.Option;
// const dataMap = {
//   position_name: 'PositionName', // 
//   supplie: 'Supplie', // 
//   job_category_code: 'JobCategoryCode', // code
//   job_category_code_name: 'JobCategoryCodeName', // name
//   job_experience_demand: 'JobExperienceDemand', // 
//   education_demand: 'EducationDemand', // 
//   salary_demand: 'SalaryDemand', // 
//   latest_come_time: 'LatestComeTime', // 
//   principal: 'Principal', // 
//   cooperator: 'Cooperator', // 
//   startor: 'Startor', // 
//   job_desc: 'JobDesc', // jd
//   created_by: 'CreatedBy', // 
//   date_created: 'DateCreated', // 
//   update_by: 'UpdateBy', // 
//   date_updated: 'DateUpdated', // 
// }
const inputDataMap = {
  principal: '', // 
  cooperator: '', // 
  recruit_num: '', // 
}
const dateDataMap = {
  latest_come_time: '', // 
}
const selectDataMap = {
  position_name: '', // 
  supplie: '', // 
  job_category_code_name: '', // name
  job_experience_demand: '', // 
  education_demand: '', // 
  salary_demand: '', // 
}
const dataMap = {
  ...inputDataMap,
  ...selectDataMap,
  ...dateDataMap,
  job_desc: '', // jd
}
const map = {}
Object.keys(inputDataMap).forEach(item => {
  map[item] = {
		component: Input,
		props: {
      size: 'large',
      placeholder: '',
      name: item,
		},
		rules: [{
			required: true,
			message: '',
		}],
	}
});
Object.keys(selectDataMap).forEach(item => {
  map[item] = {
		component: Select,
		props: {
      size: 'large',
      placeholder: '',
      name: item,
      children: <Option value='china' > China </Option>
		}
	}
});
Object.keys(dateDataMap).forEach(item => {
  map[item] = {
    component: DatePicker,
    props: {
      size: 'large',
      placeholder: '',
      name: item,
    }
  }
});
map['job_desc'] = {
  component: Textarea,
  props: {
    size: 'large',
    prefix: dataMap['job_desc'],
    name: 'job_desc',
  },
  itemClass: styles.job_desc
}
export { dataMap };
export default map;



