import React from 'react'
// import PropTypes from 'prop-types';
import SearchFilter from 'component/search-filter';
import AlertInfo from 'component/alert-info';
import FormInfo, { generator } from 'component/form-info';
import { services, urls } from 'api'

import {
  Button,
  Table,
  Modal,
  Card,
  Alert,
  Input,
} from 'components';
import styles from './index.less'

const steps = ['', '', '', '']
const CODE = ['CALANDAR_INTERVIEW', 'CALANDAR_EXAM', 'CALANDAR_IN']

export default class Calendar extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      selectedRowKeys: [], // Check here to configure the default column
      modalVisible: false,
      step: '', // 0-1-2-
      Key: '0',
      data: [],
    };
  }
  componentDidMount () {
    this.setState({
      step: 0
    })
  }
  shouldComponentUpdate (nextProps, nextState) {
    console.log('this.state.step', this.state.step, 'nextState.step', nextState.step);

    if (this.state.step !== nextState.step) {
      const url = CODE[nextState.step]
      console.log(url)
      services.post(urls[url], {
        ORGANIZATION_CODE: 'SUPPLIER_ZR'
      }, this.getData)
    }
    return true
  }

  getData = (data) => {
    // console.log('getData', data);
    this.setState({
      data
    })
  }

  onTabChange = (key, type) => {
    // console.log(key, type);
    this.setState({
      [type]: key,
      step: key,
    });
  }
  onSelectChange = (selectedRowKeys) => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({
      selectedRowKeys
    });
  }
  handleOk = () => {
    this.handleModal()
    console.log('handleOk')
  }
  handleCancel = () => {
    this.handleModal()
    console.log('handleCancel', this.state.modalVisible)
  }
  handleModal = () => {
    this.setState((state) => ({
      modalVisible: !state.modalVisible
    }))
  }
  modalLabelGenerator = (step) => {
    let modalLabel = {}
    // console.log('modalLabelGenerator', step)
    switch (parseInt(step)) {
      case 0:
        modalLabel = {
          NAME: '',
          APPOINTMENT_INTERVIEWER_TIME: '',
          INTERVIEWER: '',
          INTERVIEW_ADDRESS: '',
        }
        return modalLabel;
      case 1:
        // 
        modalLabel = {
          CHECK_RANK_LEVEL_CODE: '',
          COMPUTER_EXAME_TIME: '',
        }
        return modalLabel;
      case 2:
      // 
        modalLabel = {
          APPINT_ENTRANCE_TIME: '',
        }
        return modalLabel;
      default:
        return modalLabel;
    }
  }
  handleAction = (step) => {
    this.handleModal()
    this.setState({
      step: step
    })
    console.log('handleAction', step)
  }
  handleDelete = (step) => {

  }
  render () {
    const { step, data } = this.state;
    const filterDetail = {}
    const columns = {
      0: [
        {
          title: '',
          width: 100,
          dataIndex: 'NAME',
          key: 'NAME',
        },
        {
          title: '',
          width: 100,
          dataIndex: 'NEED_ORGANIZATION_CODE',
          key: 'NEED_ORGANIZATION_CODE',
          render: (text, record, index) => record.NEED_ORGANIZATION_NAME
        },
        {
          title: '',
          width: 100,
          dataIndex: 'CUSTOMER_MANAGER',
          key: 'CUSTOMER_MANAGER',
        },
        {
          title: '',
          dataIndex: 'RECRUIT_TRACKER',
          key: 'RECRUIT_TRACKER',
          width: 150
        },
        {
          title: ``,
          dataIndex: 'APPOINTMENT_INTERVIEWER_TIME',
          key: 'APPOINTMENT_INTERVIEWER_TIME',
          width: 150
        },
        {
          title: ``,
          dataIndex: 'ACTUAL_INTERVIEWER_TIME',
          key: 'ACTUAL_INTERVIEWER_TIME',
          width: 150
        },
    ],
      1: [
        {
          title: '',
          width: 100,
          dataIndex: 'NAME',
          key: 'NAME',
        },
        {
          title: '',
          width: 100,
          dataIndex: 'NEED_ORGANIZATION_CODE',
          key: 'NEED_ORGANIZATION_CODE',
          render: (text, record, index) => record.NEED_ORGANIZATION_NAME
        },
        {
          title: '',
          width: 100,
          dataIndex: 'CUSTOMER_MANAGER',
          key: 'CUSTOMER_MANAGER',
        },
        {
          title: '',
          dataIndex: 'RECRUIT_TRACKER',
          key: 'RECRUIT_TRACKER',
          width: 150
        },
        {
          title: '',
          dataIndex: 'COMPUTER_EXAME_TIME',
          key: 'COMPUTER_EXAME_TIME',
          width: 150
        },
        {
          title: '',
          dataIndex: 'RANK_LEVEL_CODE',
          key: '7',
          width: 150,
        }
    ],
      2: [
        {
          title: '',
          width: 100,
          dataIndex: 'NAME',
          key: 'NAME',
        },
        {
          title: '',
          width: 100,
          dataIndex: 'NEED_ORGANIZATION',
          key: 'NEED_ORGANIZATION_CODE',
          render: (text, record, index) => record.NEED_ORGANIZATION_NAME
        },
        {
          title: '',
          width: 100,
          dataIndex: 'CUSTOMER_MANAGER',
          key: 'CUSTOMER_MANAGER',
        },
        {
          title: '',
          dataIndex: 'RECRUIT_TRACKER',
          key: 'RECRUIT_TRACKER',
          width: 150
        },
        {
          title: '',
          dataIndex: 'APPINT_ENTRANCE_TIME',
          key: 'APPINT_ENTRANCE_TIME',
          width: 150
        },
        {
          title: '',
          dataIndex: 'ACTUAL_ENTRANCE_TIME',
          key: 'ACTUAL_ENTRANCE_TIME',
          width: 150
        },
        {
          title: '',
          dataIndex: 'RANK_LEVEL_CODE',
          key: 'RANK_LEVEL_CODE',
          width: 150,
        }
    ],
    };
    // console.log('datarender', data);
    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    const tabList = [{
        key: '0',
        tab: '  ',
      }, {
        key: '1',
        tab: '  ',
      }, {
        key: '2',
        tab: '  ',
    }];
    const contentList = (step) => ([
      <Button type='primary' onClick={() => this.handleAction(step)} key={`${step}_button`}>{`${steps[step]}`}</Button>,
      <Button onClick={() => this.handleAction(3)} style={{marginLeft: '16px'}} key={`${step}_delete`}></Button>,
      <Table
        rowSelection={rowSelection}
        className={styles.table}
        columns={columns[step]}
        dataSource={data}
        scroll={{ x: 1500, y: 300 }}
        key={`${step}_table`} />
    ]);
    const modalLabel = this.modalLabelGenerator(step)
    const dataMap = modalLabel
    // console.log('dataMap', dataMap)
    map = {}
    generatorParams(dataMap)
    // generatorParams(selectDataMap, Select, false, '')
    // generatorParams(DatePicker, DatePicker, false, '')
    const Info = {};
    Object.keys(map).forEach((item) => {
      Info[item] = generator({
        defaultProps: map[item].props,
        defaultRules: map[item].rules,
        type: item,
        title: dataMap[item],
        width: '100%',
      })(map[item].component)
    })
    return (
      <div className={styles.calendar_list}>
        <AlertInfo></AlertInfo>
        <div className={styles.action_area}>
          <SearchFilter columns={filterDetail} className={styles.search_filter} />
          <Modal title={this.state.step === 3 ? `${steps[this.state.step]}` : `${steps[this.state.step]}`} visible={this.state.modalVisible}
            onOk={this.handleOk} onCancel={this.handleCancel} width={800} key={`${this.state.step}_modal`}>
            <FormInfo
              onSubmit={this.onSubmit}
              className='content'
            >
              {
                this.state.notice &&
                <Alert style={{ marginBottom: 24 }} message={this.state.notice} type='error' showIcon closable />
              }
              {
                Object.keys(Info).map((item, i) => React.createElement(Info[item], {key: `${step}_${i}`, name: item}))
              }
            </FormInfo>
          </Modal>
        </div>
        <Card
          className={styles.card}
          tabList={tabList}
          activeTabKey={this.state.Key}
          onTabChange={(key) => { this.onTabChange(key, 'Key') }}
        >
          { contentList(this.state.Key) }
        </Card>
      </div>
    )
  }
}

let map = {}
function generatorParams(data, component, required, placeholder) {
  Object.keys(data).forEach(item => {
    map[item] = {
      component: component || Input,
      props: {
        size: 'large',
        placeholder: placeholder || '',
        name: item,
      },
      rules: (!required && '') || [{
        required: true,
        message: '',
      }],
    }
  });
}




