import React from 'react';

import Modal from 'components/modal';
import Form from 'components/form';
import Button from 'components/button';
import { Row, Col } from 'components/grid';
import Input from 'components/input';
import Select from 'components/select';

import styles from './index.less';


const FormItem = Form.Item;
const Option = Select.Option;


class SelfModal extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            visible: false,
            isRule: false
           }
    }
    componentWillReceiveProps(nextProps) {
        this.setState({
            visible: nextProps.visible,
        })
    }
    // 
    handleSave() {
        this.setVisible(false);
    }

    // 
    handleReset() {
     this.props.form.resetFields();
    }

    // 
    handleCancel() {
       this.setVisible(false)
    }

    setVisible(bool) {
        this.setState({
            visible: !!bool
        })
    }

    handleSelectRule(key) {
        this.setState({
            isRule: key === 'hr'
        })
    }
    renderFormItem() {
        const { getFieldDecorator } = this.props.form;
        return (
          <Col>
            <FormItem label='' labelCol={{span: 6}} wrapperCol={{span: 18}} className={styles.form_item_width}>
              {getFieldDecorator('name', {
                     rules: [{
                        required: true, message: '', whitespace: true
                     }, {pattern: /^[\u4e00-\u9fa5]{2,4}$/, message: '2-4'}]
                 })(<Input placeholder='' />)}
            </FormItem>
            <FormItem label='' labelCol={{span: 6}} wrapperCol={{span: 18}} className={styles.form_item_width}>
              {getFieldDecorator('rule', {
                  rules: [{required: true}]
              })(<Select onSelect={(key) => this.handleSelectRule(key)} placeholder=''>
                <Option key='hr' value='hr'>hr</Option>
                <Option key='interviewer' value='interviewer'></Option>
              </Select>)}
            </FormItem>
            <FormItem label='' labelCol={{span: 6}} wrapperCol={{span: 18}} className={styles.form_item_width}>
              {getFieldDecorator('position', {
                  rules: [{required: true}]
              })(<Select disabled={this.state.isRule} placeholder=''>
                <Option key='web' value='web'>web</Option>
                <Option key='java' value='java'>java</Option>
                <Option key='php' value='php'>php</Option>
                <Option key='pyhon' value='pyhon'>pyhon</Option>
              </Select>)}
            </FormItem>
            <FormItem label='' labelCol={{span: 6}} wrapperCol={{span: 18}} className={styles.form_item_width}>
              {getFieldDecorator('department', {
                  rules: [{required: true}]
              })(<Select disabled={this.state.isRule} placeholder=''>
                <Option key='' value='web'></Option>
                <Option key='' value=''></Option>
                <Option key='' value=''></Option>
                <Option key='' value=''></Option>
              </Select>)}
            </FormItem>
            <FormItem label='' labelCol={{span: 6}} wrapperCol={{span: 18}} className={styles.form_item_width}>
              {getFieldDecorator('company', {
                  rules: [{required: true, message: ''}]
              })(<Input />)}
            </FormItem>
            <FormItem label='' labelCol={{span: 6}} wrapperCol={{span: 18}} className={styles.form_item_width}>
              {getFieldDecorator('telephone', {
                  type: 'number',
                  rules: [{required: true, message: ''}]
              })(<Input />)}
            </FormItem>
            <FormItem label='' labelCol={{span: 6}} wrapperCol={{span: 18}} className={styles.form_item_width}>
              {getFieldDecorator('email', {
                  rules: [{
                      type: 'email'
                  }, {required: true}]
              })(<Input />)}
            </FormItem>
            <FormItem label='' labelCol={{span: 6}} wrapperCol={{span: 18}} className={styles.form_item_width}>
              {getFieldDecorator('account', {
                  rules: [{
                      type: 'account'
                  }, {required: true}]
              })(<Input />)}
            </FormItem>
            <FormItem label='' labelCol={{span: 6}} wrapperCol={{span: 18}} className={styles.form_item_width}>
              {getFieldDecorator('password', {
                  rules: [{
                      type: 'password'
                  }, {required: true}]
              })(<Input />)}
            </FormItem>
          </Col>
        )
    }

    render() {
      const {visible} = this.state
      return (
        <Modal title='' width={500} style={{height: '500px'}}
          className={styles.warp_modal}
          visible={visible}
          footer={[
            <Button key='handleCancel' onClick={() => this.handleCancel()}></Button>,
            <Button key='handleReset' onClick={() => this.handleReset()}></Button>,
            <Button key='handleSave' onClick={() => this.handleSave()}></Button>
          ]}
          >
          <Form onSubmit={this.handleSubmit} layout='inline' >
            <Row>
              {this.renderFormItem()}
            </Row>
          </Form>
        </Modal>
      )
    }
};

const WrappedHorizontalLoginForm = Form.create()(SelfModal);

export default WrappedHorizontalLoginForm;



