import React from 'react';

import { Input, Select, DatePicker } from 'components';

import styles from './index.less';

const { Textarea } = Input;
const Option = Select.Option;
const inputDataMap = {
  name: '',
  work_age: '',
  telephone: '',
  email: '',
  current_salary: ')',
  expect_salary: ')',
  address: '',
}
const dateDataMap = {}
const selectDataMap = {
  level: '',
  is_on_job: '',
  technical: '',
  official_academic: '',
}
const dataMap = {
  ...inputDataMap,
  ...selectDataMap,
  ...dateDataMap,
  remark: '',
}
const map = {}
Object.keys(inputDataMap).forEach(item => {
  map[item] = {
		component: Input,
		props: {
      size: 'large',
      placeholder: '',
      name: item,
		},
		rules: [{
			required: true,
			message: '',
		}],
	}
});
Object.keys(selectDataMap).forEach(item => {
  map[item] = {
		component: Select,
		props: {
      size: 'large',
      placeholder: '',
      name: item,
      children: <Option value='china' > China </Option>
		}
	}
});
Object.keys(dateDataMap).forEach(item => {
  map[item] = {
    component: DatePicker,
    props: {
      size: 'large',
      placeholder: '',
      name: item,
    }
  }
});
map['remark'] = {
  component: Textarea,
  props: {
    size: 'large',
    prefix: dataMap['remark'],
    name: 'remark',
  },
  itemClass: styles.remark
}
export { dataMap };
export default map;



